<template>
  <div>
    <mdb-card class="card-image mt-5 pt-5 main">
      <div class="text-white text-center py-5 px-4 my-5">
        <div>
          <h2 class="card-title h1-responsive pt-3 mb-5 font-bold">
            <strong>Todos los productos</strong>
          </h2>
          <p class="mx-5 mb-5">
            {{ this.selectedData }}
          </p>
        </div>
      </div>
    </mdb-card>
    <div class="container mt-5">
       <div>
    <mdb-breadcrumb class="white">
      <mdb-breadcrumb-item><router-link to="/" class="red-text">Inicio</router-link></mdb-breadcrumb-item>
      <mdb-breadcrumb-item v-if="categoria"><a class="red-text" @click="reload">Servicios</a></mdb-breadcrumb-item>
      <mdb-breadcrumb-item v-else active>Servicios</mdb-breadcrumb-item>
      <mdb-breadcrumb-item active>{{categoria}}</mdb-breadcrumb-item>
    </mdb-breadcrumb>
  </div>
      <mdb-card class="mb-3" style="background: #e4e4e4;">
        <mdb-select
          v-model="basicOptions"
          label="Filtrar por:"
          placeholder="Categora"
          color="danger"
          class="mx-4"
        />
      </mdb-card>
      <div class="row">
        <div
          class="col-md-4 my-3 mx-auto"
          v-for="item in products"
          :key="item.id"
        >
          <router-link :to="`/product/${item.id}`"
            ><item-card
              :title="item.title"
              :price="item.price"
              :nuevo="item.nuevo"
              :sale="item.sale"
              :image="item.image"
              :categoria="item.category"
              class="mx-auto"
            />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mdbCard, mdbSelect, mdbBreadcrumb, mdbBreadcrumbItem } from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbCard,
    mdbSelect,
    ItemCard,
    mdbBreadcrumb, mdbBreadcrumbItem
  },
  data() {
    return {
      basicOptions: [
        { text: "Mallas", value: "Mallas" },
        { text: "Accesorios para cercas", value: "Accesorios para cercas" },
        { text: "Concertina", value: "Concertina" },
        { text: "Alambres", value: "Alambres" },
        { text: "Postes", value: "Postes" },
        { text: "Metal desplegado", value: "Metal desplegado" },
        { text: "Tubo galvanizado", value: "Tubo galvanizado" },
        { text: "Lminas", value: "Lminas" },
        { text: "Materiales para invernaderos", value: "Materiales para invernaderos" },
      ],
      categoria: '',
      products: [...this.items],
    };
  },
  computed: {
    selectedData() {
      let response = this.basicOptions.find((option) => {
        return option.selected === true;
      });
      if (response) {
        this.changeCategory(response.value);
        return response.value;
      }
      return "";
    },
  },
  methods: {
    changeCategory(nueva) {
      this.categoria = nueva;
    },
    reload() {
      this.categoria = ''
    }
  },
  props: {
    items: Array,
  },
  watch: {
    categoria(nueva) {
      if (nueva != '') {
        let temp = [...this.items];
      let changes = temp.filter((item) => item.category == nueva);
      this.products = changes;
      } else {
        this.products = [...this.items];
      }
    },
  },
  mounted() {
    if (this.$route.params.category) {
      this.categoria = this.$route.params.category
      let temp = [...this.items];
      let changes = temp.filter((item) => item.category == this.$route.params.category);
      this.products = changes;
  }
  },
};
</script>

<style>
.main {
  background: no-repeat center fixed;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("../assets/cercas/2.jpg");
    color: #e4e4e4
}
</style>