// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'bootstrap-css-only/css/bootstrap.min.css'
import 'mdbvue/build/css/mdb.css'
import '@fortawesome/fontawesome-free/css/all.min.css'

import 'vue-preloaders/dist/vue-preloaders.css'
import VuePreloaders from 'vue-preloaders'
import preloader from './components/preloader.vue'


Vue.config.productionTip = false

Vue.use(VuePreloaders, { loaders: { 
  test:
    {
      "component": preloader,
      "overlayStyle": {
        "backgroundColor": "#ffff",
        opacity: 1,
      },
      cssStyle: { "z-index": 99999,}
    },
    gifloader: {
      "assetSrc": "https://i.giphy.com/media/jAYUbVXgESSti/giphy.webp",
      "overlayStyle": {
        "backgroundColor": "#ecf0ef",
        "opacity": 1,
        "z-index": 99998
      },
      assetStyle: { "z-index": 99999 }
    }
} })

new Vue({
  render: h => h(App),
  router,
  components: { App }
  }).$mount('#app')
  
  
